@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);

body {
  font-family: "Roboto Condensed" sans-serif;
}

:root{
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(255,255,255,0);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
}

.App {

  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/**navbar****/

nav{
  z-index: 2;
}

.logo{
   
  width:16.25rem;

}

/****menu link***/

nav a.nav-link {
  /*font-weight: 500;*/
  letter-spacing: 0.0375;
  cursor: pointer;
}

nav ul li{
  text-transform: uppercase;
}

.navbar-nav >li >a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem  solid rgba(255,255,255,0);
  border-top: 0.1875rem  solid var(--primary-dark);
}

.navbar-nav >li >a.active, .navbar-nav >li > a.active:focus {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav >li >a:hover{
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

/*** Header***/
.header-wraper {
  position: relative;
  background: url(data:image/jpeg;base64,);
  background-size: cover;
  background-position: center;
  height:90vh;
}
.main-info{
   display: flex;
   position: absolute;
   top:0;
   left:0;
   right:0;
   bottom:0;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   text-align:center;
   font-family: "Roboto Condensed" sans-serif;
   z-index: 3;
}

.main-info h1{

  color: #f9ab00;

  color: var(--primary-orange);
  text-transform: uppercase;
}

/*** typed Text***/

.typed-text{
  font-size:2rem;
  color: #fff;
  color: var(--primary-white);
}

@media(max-width: 768px) {
  .main-info h1{
    font-size: 1.7gitrem !important;
  }

  .typed-text{
    font-size: 1.3rem !important;
  }

}

/****button****/
.btn-main-offer{
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #fff;
  color: var(--primary-white);
  margin: 2rem;
  
}
.btn-main-offer:hover{
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  color: #fff;
  color: var(--primary-white);
  transition: .2s ease-in-out;
}

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width:100vw;
  z-index: 1;
}

